import Vue from 'vue'
import Vuex from 'vuex'

import user from './store.user.js';

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		counter: 0,
	},
	mutations: {
		increment : function( state, input ){
			state.counter += input;
		},
		decrement : function( state, input ){
			state.counter -= input;
		},	
	},	
	getters : {
		count : function( state ){
			return state.counter;
		},		
	},
	actions: {

	},
	modules : {
		user
	}
})
